/*todo make a theme.css*/
* {
  font-family: 'Open Sans', 'Helvetica', sans-serif;
  color: #333;
  line-height: 1.2; }

h1 {
  font-family: 'Playfair Display', 'Times New Roman', Times, serif;
  font-weight: 900;
  height: auto; }

.content-outer {
  display: flex;
  justify-content: center;
  min-height: 100vh;
  background-color: #f0f0f0;
  color: #333;
  padding: 0 5%; }
  .content-outer .divider {
    background-color: #333;
    height: 1px;
    width: 30%;
    margin: 10px 0; }
  .content-outer .left-navbar {
    width: 20%; }
  .content-outer .content {
    width: 70%; }
    .content-outer .content .word {
      display: inline-block;
      font-family: 'Times New Roman', Times, serif;
      font-size: 1.5rem;
      margin: 5px; }
    .content-outer .content .next-word {
      display: inline-block;
      height: 1.5rem;
      width: 0.5rem;
      margin: 5px;
      vertical-align: bottom;
      background-color: #CEDFD9;
      -webkit-animation: blink 2s infinite;
              animation: blink 2s infinite; }

@-webkit-keyframes blink {
  0% {
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@keyframes blink {
  0% {
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    opacity: 0; } }

.logo-box {
  border: 5px solid #CEDFD9;
  padding: 2%; }
  .logo-box .form {
    background-color: green;
    outline: none;
    border: none;
    border-bottom: 2px solid #333; }
  .logo-box p {
    font-family: "Playfair Display", "Times New Roman", Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    width: 100%;
    color: #333;
    font-weight: 900;
    margin: 0; }

